/* Navigation  */

.nav-trigger {
	display: inline-block;
	cursor: pointer;
	transition-property: opacity, filter;
	transition-duration: 0.35s;
	transition-timing-function: linear;
	transition-delay: 0s;
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	overflow: visible;
	margin-right: 0;
	top: 5px;
	position: relative;
	outline: 0;
	&:hover {
		opacity: 0.7;
	}
}
.nav-trigger.active {
	outline: 0;
	&:hover {
		opacity: 0.7;
	}
}
.nav-trigger-box {
	width: 28px;
	height: 24px;
	display: inline-block;
	position: relative;
}
.nav-trigger-inner {
	display: block;
	top: 50%;
	margin-top: -2px;
	width: 28px;
	height: 4px;
	background-color: #000;
	border-radius: 4px;
	position: absolute;
	transition-property: transform;
	transition-duration: 0.35s;
	transition-timing-function: ease;
	&::after {
		width: 14px;
		height: 4px;
		background-color: #000;
		border-radius: 4px;
		position: absolute;
		transition-property: transform;
		transition-duration: 0.35s;
		transition-timing-function: ease;
		content: "";
		display: block;
		bottom: -10px;
	}
	&::before {
		width: 28px;
		height: 4px;
		background-color: #000;
		border-radius: 4px;
		position: absolute;
		transition-property: transform;
		transition-duration: 0.35s;
		transition-timing-function: ease;
		content: "";
		display: block;
		top: -10px;
	}
}
.nav-trigger-slider {
	.nav-trigger-inner {
		top: 2px;
		&::before {
			top: 10px;
			transition-property: transform, opacity;
			transition-timing-function: ease;
			transition-duration: 0.35s;
		}
		&::after {
			top: 20px;
		}
	}
}
.nav-trigger-slider.active {
	.nav-trigger-inner {
		width: 36px;
		transform: translate3d(0, 10px, 0) rotate(-45deg);
		&::before {
			width: 36px;
			transform: rotate(45deg) translate3d(5.71429px, -6px, 0);
			opacity: 0;
		}
		&::after {
			width: 36px;
			transform: translate3d(0, -20px, 0) rotate(90deg);
		}
	}
}
.active-menu {
	position: relative;
	transition: all 1s linear 0s;
	&:before {
		content: '';
		background: #B1D95B;
		left: 0;
		right: 0;
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: -1;
		transition: all 1s linear 0s;
		padding-bottom: 90px;
	}
}
.navbar-collapse {
	padding-top: 0;
	ul.navbar-nav {
		padding-top: 56px;
		padding-bottom: 100px;
		.nav-item {
			margin-right: 35px;
			transition: all 0.5s linear 0s;
		}
		.nav-link {
			color: #fff;
			font-size: 26px;
			font-weight: 500;
			padding-left: 55px;
			padding-bottom: 11px;
			border-bottom: 1px solid #000000;
			transition: all 0.5s linear 0s;
		}
	}
}


@media only screen and (min-width:992px) {
.navbar-collapse {
		ul.navbar-nav {
			padding-top: 8px;
			padding-right: 0;
			margin-right: 0 !important;
			padding-bottom: 0;
			.nav-link {
				color: #000;
				font-size: 12px;
				font-weight: 400;
				padding-left: 0;
				padding-right: 0;
				border-bottom: 0;
				position: relative;
				&:hover {
					color: #B1D95B;
					padding-bottom: 6px;
					&:after {
						display: block;
						width: 100%;
					}
				}
				&:after {
					display: none;
					content: '';
					height: 3px;
					position: absolute;
					bottom: 0;
					background: #B1D95B;
					left: 0;
					right: 0;
					width:0;
				}
			}
		}
		justify-content: flex-end;
	}
}

@media only screen and (min-width:1200px) {
	
